<template>
  <div class="table-box">
    <a-table
      class="table"
      :pagination="{showSizeChanger:true,showQuickJumper:true,total,current,pageSize, showTotal:total => `共 ${total} 条记录`}"
      @change="onchange"
      :rowKey="(v,i)=>i"
      :columns="table"
      :dataSource="tableData"
    >
      <template slot="processCode" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processCode"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processName" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processName"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processRemark" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processRemark"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processHours" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processHours"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processAuxiliaryHours" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processAuxiliaryHours"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processMachiningCost" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processMachiningCost"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processEquipment" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processEquipment"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processFixture" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processFixture"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processCheck" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processCheck"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processTool" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processTool"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="processWorkCenter" slot-scope="text, v,index">
        <template v-if="edit==index">
          <a-input v-model="v.processWorkCenter"></a-input>
        </template>
        <template v-else>{{text}}</template>
      </template>
      <template slot="operation" slot-scope="text,v,i">
        <template v-if="edit==i">
          <l-link @click="saveEdit(v)">保存</l-link>
          <l-link @click="getTable()">取消</l-link>
        </template>
        <template v-else>
          <l-link @click="operation(false,v,i)">编辑</l-link>
          <l-link @click="operation(true,v,i)">删除</l-link>
        </template>
      </template>
    </a-table>
  </div>
</template>
<script>
import { getProcess, saveProcess, deleteProcess } from "@/api/craft";
export default {
  props: {
    code: {
      default: ""
    },
    name: {
      default: ""
    }
  },
  data() {
    return {
      table: [
        {
          title: "工序编码",
          dataIndex: "processCode",
          scopedSlots: { customRender: "processCode" }
        },
        {
          title: "工序名称",
          dataIndex: "processName",
          scopedSlots: { customRender: "processName" }
        },
        {
          title: "工序描述",
          dataIndex: "processRemark",
          scopedSlots: { customRender: "processRemark" }
        },
        {
          title: "工时",
          dataIndex: "processHours",
          scopedSlots: { customRender: "processHours" }
        },
        {
          title: "辅助工时",
          dataIndex: "processAuxiliaryHours",
          scopedSlots: { customRender: "processAuxiliaryHours" }
        },
        {
          title: "加工成本",
          dataIndex: "processMachiningCost",
          scopedSlots: { customRender: "processMachiningCost" }
        },
        {
          title: "设备",
          dataIndex: "processEquipment",
          scopedSlots: { customRender: "processEquipment" }
        },
        {
          title: "夹具",
          dataIndex: "processFixture",
          scopedSlots: { customRender: "processFixture" }
        },
        {
          title: "检具",
          dataIndex: "processCheck",
          scopedSlots: { customRender: "processCheck" }
        },
        {
          title: "刀具",
          dataIndex: "processTool",
          scopedSlots: { customRender: "processTool" }
        },
        {
          title: "工作中心",
          dataIndex: "processWorkCenter",
          scopedSlots: { customRender: "processWorkCenter" }
        },
        {
          title: "操作",
          dataIndex: "operation",
          scopedSlots: { customRender: "operation" }
        }
      ],
      tableData: [],
      total: 0,
      pageSize: 10,
      current: 1,
      edit: null
    };
  },
  mounted() {
    this.getTable();
  },
  methods: {
    async getTable(current = this.current) {
      let { pageSize, code, name } = this;
      let res = await getProcess({
        pageNow: current,
        pageSize,
        processCode: code,
        processName: name
      });
      if (res.data.code != this.$global.successCode) return;
      this.current = current;
      this.edit = null;
      this.total = res.data.data.total;
      this.tableData = res.data.data.list;
    },
    async saveEdit(value) {
      let res = await saveProcess(value);
      if (res.data.code != this.$global.successCode) return;
      this.edit = null;
      this.getTable();
    },
    onchange(v) {
      let { current, pageSize } = v;
      Object.assign(this, { current, pageSize });
      this.getTable();
    },
    async operation(del = false, v, i) {
      if (del) {
        let res = await deleteProcess(v.processId);
        if (res.data.code != this.$global.successCode) return;
        this.getTable();
      } else {
        if (this.edit != null) {
          return this.$message.warning("请保存信息！！！");
        }
        this.edit = i;
      }
    }
  }
};
</script>
<style lang="less" scoped>
.table-box {
  height: 100%;
  width: 100%;
  overflow-x: auto;
  .table {
    input {
      width: 120px;
    }
  }
}
</style>