import http from '@/utils/AxiosRequest';

export let getProcess = (params) => {
    return http({ url: `/process/list`, method: 'GET', params })
}
export let getDetail = (processCode) => {
    return http({ url: `/process/${processCode}`, method: 'GET' })
}
export let saveProcess = (data) => {
    return http({ url: `/process`, data, method: 'put' })
}
export let deleteProcess = (processId) => {
    return http({ url: `/process`, params: { processId }, method: 'delete' })
}

export let addCraft = (data) => {
    return http({ url: `/technology`, data, method: 'post' })
}
export let updateCraft = (data) => {
    return http({ url: `/technology`, data, method: 'put' })
}
export let getCraftDetail = (id) => {
    return http({ url: `/technology/${id}`, method: 'GET' })
}
export let deleteCraft = (id) => {
    return http({ url: `/technology/${id}`, method: 'delete' })
}
export let getCraft = (params) => {
    return http({ url: `/technology/list`, method: 'GET', params })
}
export let searchProduct = (productCode) => {
    return http({ url: `/parts`, method: 'GET', params: { productCode } })
}
