<template>
  <a-modal width="1000px" title="添加工序信息" v-model="visible" @ok="submit">
    <a-form class="add-info-form" :form="form" layout="inline">
      <a-form-item label="工序编码" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input
          v-decorator="['processCode',{rules: [{ required: true, message: '请输入工序编码' }]}]"
          placeholder="请输入工序编码"
        />
      </a-form-item>
      <a-form-item label="工序名称" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input
          v-decorator="['processName',{rules: [{ required: true, message: '请输入工序名称' }]}]"
          placeholder="请输入工序名称"
        />
      </a-form-item>
      <a-form-item label="工时" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['processHours']" placeholder="请输入工时" />
      </a-form-item>
      <a-form-item label="辅助工时" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['processAuxiliaryHours']" placeholder="请输入辅助工时" />
      </a-form-item>
      <a-form-item label="人工成本" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['processLaborCost']" placeholder="请输入人工成本" />
      </a-form-item>
      <a-form-item label="加工成本" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['processMachiningCost']" placeholder="请输入加工成本" />
      </a-form-item>
      <a-form-item label="设备" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['processEquipment']" placeholder="请输入设备" />
      </a-form-item>
      <a-form-item label="夹具" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['processFixture']" placeholder="请输入夹具" />
      </a-form-item>
      <a-form-item label="检具" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['processCheck']" placeholder="请输入检具" />
      </a-form-item>
      <a-form-item label="刀具" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['processTool']" placeholder="请输入刀具" />
      </a-form-item>
      <a-form-item label="工作中心" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['processWorkCenter']" placeholder="请输入工作中心" />
      </a-form-item>
      <a-form-item label="备注" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-textarea v-decorator="['processRemark']" placeholder="请输入备注" :rows="4" />
      </a-form-item>
    </a-form>
  </a-modal>
</template>
<script>
import { saveProcess } from "@/api/craft";
export default {
  data() {
    return {
      visible: false,
      form: this.$form.createForm(this),
      labelCol: { span: 7 },
      wrapperCol: { span: 15 },
      formData: {
        processAuxiliaryHours: "",
        processCheck: "",
        processCode: "",
        processEquipment: "",
        processFixture: "",
        processHours: "",
        processId: "",
        processLaborCost: "",
        processMachiningCost: "",
        processName: "",
        processOrder: "",
        processRemark: "",
        processTool: "",
        processWorkCenter: ""
      }
    };
  },
  mounted() {
  },
  methods: {
    async submit() {
      this.form.validateFields({ first: true }, async valid => {
        if (valid) return;
        let value = this.form.getFieldsValue();
        value = Object.assign(this.formData, value);
        let res = await saveProcess(value);
        if (res.data.code != this.$global.successCode) return;
        this.visible = false;
        this.form.resetFields();
        this.$emit("getTable");
      });
    }
  }
};
</script>
<style lang="less">
.add-info-form {
  .ant-form-item {
    width: 50%;
    margin: 10px 0;
  }
  .ant-form-item-required::before {
    font-weight: bold;
  }
}
</style>